<template>
    <div class="display">
        <a-form-item>
            <div style="color:#4CAF50; text-align: center; text-transform: uppercase;">{{ inboxInfo }}</div>
        </a-form-item>
        <a-table :columns="columns" :data="data" :scroll="scroll" :scrollbar="scrollbar" class="table-dis" />
        <a-form-item class="reply">

            <a-form-item field="respond" label="Message">
                <a-input v-model="form.respond" placeholder="please enter here your message..."></a-input>
            </a-form-item>

            <a-form-item>
                <a-button type="primary" status="success" @click="replyMessage">Send</a-button>
            </a-form-item>
        </a-form-item>

    </div>
</template>

<script>
import { inject, onMounted, reactive, ref } from 'vue';
import { Modal } from '@arco-design/web-vue';

export default {
   
    setup() {

        // const scrollbar = ref(true);
        const scroll = {
            y: 300
        };
        const scrollPercent = {
            y: '100%'
        };

        const inboxInfo = ref()
        const $axios = inject("$axios")
        const columns = [


            {
                title: 'Me',
                dataIndex: 'Questions'

            }
            , {
                title: 'Admin',
                dataIndex: 'Reply'
            }

        ];
        const data = reactive([]);

        onMounted(() => {
            $axios({
                url: "/anki/feedBack.do",
                method: "post",
                headers: {}
            }).then((res) => {
                if (res.data.status == 200) {
                    inboxInfo.value = "You have new messages...";
                    console.log(data);
                    var userData = res.data.data;
                    for (var i = 0; i < userData.length; i++) {
                        var obj = userData[i];
                 
                        obj.Questions = obj.questions;
                        obj.Reply = obj.answers;
                        data.push(obj);
                    }

                } else {

                    inboxInfo.value = res.data.info

                }

            })
        })

        const form = reactive({
          
            respond: ''
        })
        
        const $router = inject("$router");
        const replyMessage = () => {

            $axios({
                url: "/anki/help.do",
                method: "post",
                data: "questions=" + form.respond,
                headers: {

                }
            }).then((res) => {
                if (res.data.status == 200) {
                    Modal.success({
                        title: 'success',
                        content: res.data.info
                    });
                    $router.push("/home");
                    
                }
            })
        }

        return {
            columns,
            data,
            scroll,
            scrollPercent,
            inboxInfo,
            replyMessage,
            form


        }
    },
}
</script>

<style scoped>
.display {
    margin-top: 18px;
    height: 90%;
    width: 87%;
    padding: 75px;
}

.reply {
    border: 2px solid #4CAF50;
    border-radius: 8px;
    padding: 20px;
}
</style>
