<template>
    <div style="color: red; ">

        <div class="card">
            <div class="inner-box" id="card">
                <div class="card-front">
                    <h2>SLOTH-HELPER</h2>
                    <a-typography-title class="title">
                        Sign Up
                    </a-typography-title>
                    <img src="../assets/logo.jpg">


                    <a-form :model="form" class="touch">

                        <a-form-item field="userName" label="User Name">
                            <a-input v-model="form.userName" placeholder="please enter your user name..." />
                        </a-form-item>

                        <a-form-item field="account" label="Account">
                            <a-input v-model="form.account" placeholder="please enter your email..." />
                        </a-form-item>

                        <a-form-item field="password" label="Password">
                            <a-input-password v-model="form.password" placeholder="Enter Password" />
                        </a-form-item>

                        <a-form-item field="Confirmpassword" label="Confirm Password">
                            <a-input-password v-model="form.Confirmpassword" placeholder="Retype Password" />
                        </a-form-item>

                        <a-form-item field="verification" label="verification key">
                            <a-input-password v-model="form.verification"
                                placeholder="Key will be used to reset password" />
                        </a-form-item>

                        <a-form-item>
                            <a-button type="primary" status="success" @click="handleSubmit">Sign Up</a-button>
                        </a-form-item>
                        <a-form-item>
                            <div style="color: red">{{ logininfo }}</div>
                        </a-form-item>


                        <a-space>

                            <router-link to="/">Alraedy have an account? </router-link>
                        </a-space>

                    </a-form>

                </div>
            </div>
        </div>
    </div>
</template>

<script>

import { inject, reactive, ref } from 'vue'
import { Modal } from '@arco-design/web-vue';
export default {
    name: "SignUpComponent",
    setup() {


        const logininfo = ref()
        const form = reactive({
           userName:'',
            account: '',
            password: '',
            Confirmpassword:'',
            verification:''
        })
        const $axios = inject("$axios");
       
        const handleSubmit = () => {
            console.log(form.account);
            $axios({
                url: "/anki/signUp.do",
                method: "post",
                data: "user_name=" + form.userName + "&email=" + form.account + "&password=" + form.password
                    + "&confirmPassword=" + form.Confirmpassword + "&verification=" + form.verification,
                headers: {

                }
            }).then((res) => {
                if (res.data.status == 200) {
                    console.log(res);
                    Modal.success({
                        title: 'Sign Up Successful',
                        content: res.data.info
                    });

                } else {
                    Modal.error({
                        title: 'Error',
                        content: res.data.info
                    });
                }
            })

        }

        return {
            form,
            handleSubmit,

            logininfo
        }
    },
}
</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
}



.card {

    width: 350px;
    height: 600px;

    perspective: 1000px;
}

/* la rotation se passe ici sur inner */
.inner-box {
    margin-left: 450px;
    width: 100%;
    height: 100%;
    /*transform: rotateY(-180deg);*/
    transform-style: preserve-3d;
    transition: transform 3s;
}

.card-front {

    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-image: linear-gradient(rgba(0, 0, 0, 0.904), rgba(0, 0, 0, 0.918)), url(../assets/logo.jpg);
    padding: 55px;
    box-sizing: content-box;
    backface-visibility: hidden;

}


.card h2 {
    font-size: normal;
    font-size: 24px;
    text-align: center;
    margin-bottom: 10px;

}

img {
    width: 40%;
    position: relative;
    left: 20px;
    justify-items: center;


}








.card .btn {
    margin-top: 0px;
}

.card a {
    color: white;
    text-decoration: none;
    display: block;
    text-align: center;
    font-size: 13px;
    margin-top: 8px;

}

.touch .text {
    padding-top: 15px;
    color: orange;
}
</style>