<template>
    <div class="display">
        <a-typography-title :heading="2" style="text-align: center;text-transform: uppercase;
        color: #4CAF50;">

            All registered users
        </a-typography-title>
        <a-table :columns="columns" :data="data" :sticky-header="100" />
    </div>
</template>
  
<script>
import { inject, onMounted, reactive } from 'vue';

export default {
    setup() {
        const $axios = inject("$axios")
        const columns = [
            {
                title: 'Name',
                dataIndex: 'userName',
            },
            {
                title: 'Email',
                dataIndex: 'Email',
            }
        ];

        var data = reactive([]);

        onMounted(() => {
            $axios({
                url: "/ankiAdmin/showAllUsers.do",
                method: "post",
                headers: {}
            }).then((res) => {
                console.log(data);
                var userData = res.data.data;
                for (var i = 0; i < userData.length; i++) {
                    var obj = userData[i];
                    obj.userName = obj.user_name;
                    obj.Email = obj.email;
                    data.push(obj);
                }
            })
        });

        return {
            columns,
            data
        }
    },
}
</script>
<style scoped>
.display {
    margin-top: 3px;
    border: 1px solid gray;
    padding: 8px;
}
</style>